.submit-btn {
  opacity: 1;
  outline: 0;
  color: #fff;
  font-size: 20px;
  line-height: 40px;
  position: relative;
  text-align: center;
  letter-spacing: 1px;
  display: inline-block;
  text-decoration: none;
  font-family: "Open Sans";
  text-transform: uppercase;
  cursor: pointer;
}

.submit-btn::after {
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  color: rgba(255, 255, 255, 0.76);
  display: block;
  transition: 0.5s;
  position: absolute;
  background: #333333;
  content: attr(data-back);
  transform: translateY(50%) rotateX(90deg);
}

.submit-btn::before {
  top: 0;
  left: 0;
  opacity: 1;
  color: #333333;
  display: block;
  padding: 0 30px;
  line-height: 40px;
  transition: 0.5s;
  position: relative;
  background: inherit;
  content: attr(data-front);
  transform: translateY(0) rotateX(0);
}

.submit-btn:hover::after {
  opacity: 1;
  transform: translateY(0) rotateX(0);
}

.submit-btn:hover::before {
  opacity: 0;
  transform: translateY(-50%) rotateX(90deg);
}

